import cmdNavBar from "@/components/cmd-nav-bar/cmd-nav-bar.vue"
import cmdPageBody from "@/components/cmd-page-body/cmd-page-body.vue"
import cmdTransition from "@/components/cmd-transition/cmd-transition.vue"
import cmdCelItem from "@/components/cmd-cell-item/cmd-cell-item.vue"
import cmdAvatar from "@/components/cmd-avatar/cmd-avatar.vue"
//import lotusAddress from "../../components/Winglau14-lotusAddress/Winglau14-lotusAddress.vue";
export default {
	components: {
		cmdNavBar,
		cmdPageBody,
		cmdTransition,
		cmdCelItem,
		cmdAvatar,
		//		lotusAddress
	},

	data() {
		return {
			icon: '',
			nickName: '',
			phone: '',
			location: ''
		};
	},
	onShow() {
		this.$forceUpdate();
		let info = uni.getStorageSync("detail")
		console.log(info)
		this.icon = info.icon;
		this.nickName = info.nickName;
		var phone = info.phone;
		phone = this.transformStr(phone, 4, 8, '*****');
		this.phone = phone
		if (info.location != null) {
			this.location = info.location;
		}
	},

	methods: {

		transformStr(oldStr, start, end, replaceStr) {
			return oldStr.substring(0, start - 1) + replaceStr + oldStr.substring(end, oldStr.length);
		},
		fnClick(type) {
			if (type == 'modify') {
				uni.navigateTo({
					url: '/pages/user/modify/modify'
				})
			} else if (type == 'login') {
				uni.navigateTo({
					url: '/pages/login/login'
				})
				uni.clearStorage();
			}
		}
	}
}
